import { useState, useEffect } from "react";
import liff from "@line/liff"; 
import "./styles.css";

// 
const COMMON_OPTIONS = [
  { text: " ", score: 4 },
  { text: " ", score: 3 },
  { text: " ", score: 2 },
  { text: " ", score: 1 },
];

const questions = [
  { id: 1, text: " " },
  { id: 2, text: " " },
  { id: 3, text: " " },
  { id: 4, text: " " },
  { id: 5, text: " " },
  { id: 6, text: " " },
  { id: 7, text: " " },
  { id: 8, text: " " },
  { id: 9, text: " " },
];

const results = [
  { type: "A", title: "", image: "/image/type-a.png", minScore: 0, maxScore: 4 },
  { type: "B", title: "", image: "/image/type-b.png", minScore: 5, maxScore: 8 },
  { type: "C", title: "", image: "/image/type-c.png", minScore: 9, maxScore: 12 },
  { type: "D", title: "", image: "/image/type-d.png", minScore: 13, maxScore: 16 },
  { type: "E", title: "", image: "/image/type-e.png", minScore: 17, maxScore: 20 },
  { type: "F", title: "", image: "/image/type-n.png", minScore: 21, maxScore: 24 },
  { type: "G", title: "", image: "/image/type-g.png", minScore: 25, maxScore: 28 },
  { type: "H", title: "", image: "/image/type-h.png", minScore: 29, maxScore: 999 },
];
// 

export default function Diagnosis() {
  const [isStarted, setIsStarted] = useState(false);
  const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);
  const [score, setScore] = useState(0);
  const [finishedResult, setFinishedResult] = useState(null);
  const [userName, setUserName] = useState("");

  useEffect(() => {
    liff
      .init({ liffId: "2009029412-d1KRC9Mp" })
      .then(() => {
        if (liff.isLoggedIn()) {
          liff.getProfile().then((profile) => {
            setUserName(profile.displayName);
          }).catch((err) => console.error(err));
        }
      })
      .catch((err) => console.error(err));
  }, []);

  const handleStart = () => setIsStarted(true);
  const handleRestart = () => {
    setIsStarted(false);
    setCurrentQuestionIndex(0);
    setScore(0);
    setFinishedResult(null);
  };
  const handleClickAnswer = (optionScore) => {
    const newScore = score + optionScore;
    setScore(newScore);
    const nextQuestion = currentQuestionIndex + 1;
    if (nextQuestion < questions.length) {
      setCurrentQuestionIndex(nextQuestion);
    } else {
      const result = results.find((r) => newScore >= r.minScore && newScore <= r.maxScore);
      setFinishedResult(result);
    }
  };

  return (
    <div className="diagnosis-app-wrapper">
      
      <div className="diagnosis-container">
        
        <div className="App">
          {finishedResult ? (
            /*  */
            <div className="result-screen">
              <h2>{userName}<br />{finishedResult.title}</h2>
              <img src={finishedResult.image} alt={finishedResult.title} className="result-image" />
              <div style={{ marginTop: "40px", borderTop: "1px solid #eee", paddingTop: "20px", width: "100%" }}>
                <button onClick={handleRestart} className="restart-btn"></button>
              </div>
            </div>
          ) : !isStarted ? (
            /*  */
            <div className="start-screen">
              <div className="line-icon"></div>
              <div className="speech-bubble">
                <p>{userName}</p>
                <p>9</p>
                <p></p>
              </div>
              <img src="/image/start.png" alt="" className="main-image" />
              <button onClick={handleStart} className="start-btn"> </button>
            </div>
          ) : (
            /*  */
            <div className="question-screen" key={currentQuestionIndex}>
              <div className="line-icon"></div>
              <div className="speech-bubble">
                <div className="progress-text">Q.{questions[currentQuestionIndex].id} / {questions.length}</div>
                <h2 className="question-text">{questions[currentQuestionIndex].text}</h2>
              </div>
              <div className="button-container">
                {COMMON_OPTIONS.map((option) => (
                  <button key={option.text} onClick={() => handleClickAnswer(option.score)} className="diagnosis-option-btn">
                    {option.text}
                  </button>
                ))}
              </div>
            </div>
          )}
        </div>
        
      </div>
    </div>
  );
}